import { getHouseImagePath } from '@/infrastructure/utils/imagePathUtils';

/**
 * 
 * 
 */
export class House {
  /**
   * 
   * @param {Object} options 
   * @param {string} options.id ID
   * @param {string} options.name 
   * @param {number} options.price 
   * @param {string} options.description 
   * @param {string} options.specialFeature 
   * @param {string} options.purchaseCondition 
   * @param {string} options.image 
   */
  constructor({
    id,
    name,
    price,
    level = 1,
    description = '',
    specialFeature = '',
    purchaseCondition = '',
    image = ''
  }) {
    this.id = id;
    this.name = name;
    this.price = price;
    this.level = level;
    this.description = description;
    this.specialFeature = specialFeature;
    this.purchaseCondition = purchaseCondition || `${price}`;
    this.image = image || getHouseImage(id);
  }

  /**
   * 
   * @param {number} money 
   * @returns {boolean} 
   */
  canBePurchasedWith(money) {
    return money >= this.price;
  }

  /**
   * /
   * @returns {number} /1-10
   */
  getValueRating() {
    // /
    if (this.price < 400000) return 7; // 
    if (this.price < 800000) return 6;
    if (this.price < 1500000) return 5;
    if (this.price < 2500000) return 4;
    return 3; // 
  }
}

/**
 *  - 1OOP
 * @param {Object} options 
 * @returns {House} 
 */
export function createHouse(options) {
  return new House(options);
}

/**
 *  - 2
 * @param {string} id ID
 * @param {string} name 
 * @param {number} price 
 * @param {string} description 
 * @param {string} specialFeature 
 * @returns {House} 
 */
export function createHouseLegacy(id, name, price, description, specialFeature) {
  return new House({
    id,
    name,
    price,
    description,
    specialFeature,
    purchaseCondition: `${price}`,
    image: getHouseImage(id)
  });
}

/**
 * 
 * @param {string} houseId ID
 * @returns {string} 
 */
function getHouseImage(houseId) {
  try {
    // houseId
    if (!houseId || houseId === 'undefined' || houseId === 'null') {
      return '/resources/assets/images/house_1.jpeg';
    }

    // 
    return getHouseImagePath(houseId);
  } catch (err) {
    console.warn(':', err);

    // 
    const imageMap = {
      'apartment': '/resources/assets/images/house_1.jpeg',
      'second_hand': '/resources/assets/images/house_2.jpeg',
      'highend': '/resources/assets/images/house_3.jpeg',
      'villa': '/resources/assets/images/house_4.jpeg',
      'mansion': '/resources/assets/images/house_5.jpeg'
    };

    // ID1-5
    if (!isNaN(parseInt(houseId))) {
      // 
      const parsedId = parseInt(houseId);
      // NaN
      const imageIndex = parsedId ? Math.max(1, Math.min(5, (parsedId % 5) || 1)) : 1;
      return `/resources/assets/images/house_${imageIndex}.jpeg`;
    }

    return imageMap[houseId] || '/resources/assets/images/house_1.jpeg';
  }
}

