/**
 * 
 */
export const ProductCategory = {
  DAILY: 'DAILY',           // 
  FOOD: 'FOOD',             // 
  ELECTRONICS: 'ELECTRONICS', // 
  LUXURY: 'LUXURY',         // 
  COLLECTIBLE: 'COLLECTIBLE' // 
};

/**
 * 
 * 
 */
export class Product {
  /**
   * 
   * @param {Object} options 
   * @param {string|number} options.id ID
   * @param {string} options.name 
   * @param {string} options.description 
   * @param {number} options.basePrice 
   * @param {number} options.minPrice 
   * @param {number} options.maxPrice 
   * @param {number} options.volatility (1-10)
   * @param {string} options.category 
   * @param {number} options.size ()
   * @param {string} options.icon 
   * @param {Array<string>} options.availableAt 
   */
  constructor({
    id,
    name,
    description = '',
    basePrice,
    minPrice,
    maxPrice,
    volatility = 5,
    category = ProductCategory.DAILY,
    size = 1,
    icon = 'box',
    availableAt = []
  }) {
    this.id = id;
    this.name = name;
    this.description = description;
    this.basePrice = basePrice;
    this.minPrice = minPrice;
    this.maxPrice = maxPrice;
    this.volatility = Math.min(Math.max(volatility, 1), 10); // 1-10
    this.category = category;
    this.size = size;
    this.icon = icon;
    this.availableAt = availableAt;
  }
  
  /**
   * 
   * @returns {Object} 
   */
  getPriceRange() {
    return {
      min: this.minPrice,
      max: this.maxPrice,
      range: this.maxPrice - this.minPrice
    };
  }
  
  /**
   * 
   * @param {number} price 
   * @returns {boolean} 
   */
  isValidPrice(price) {
    return price >= this.minPrice && price <= this.maxPrice;
  }
  
  /**
   * 
   * @param {number} price 
   * @returns {number} 
   */
  adjustPriceToRange(price) {
    return Math.max(this.minPrice, Math.min(price, this.maxPrice));
  }
}

/**
 *  - 1OOP
 * @param {Object} options 
 * @returns {Product} 
 */
export function createProduct(options) {
  return new Product(options);
}

/**
 *  - 2
 * @param {string|number} id ID
 * @param {string} name 
 * @param {string} category 
 * @param {number} minPrice 
 * @param {number} maxPrice 
 * @param {number} basePrice 
 * @returns {Object} 
 */
export function createProductLegacy(id, name, category, minPrice, maxPrice, basePrice) {
  return new Product({
    id,
    name,
    category,
    minPrice,
    maxPrice,
    basePrice,
    volatility: calculateVolatility(category),
    availableAt: getAvailableLocations(category)
  });
}

/**
 * 
 * @param {string} category 
 * @returns {number} (1-10)
 */
function calculateVolatility(category) {
  switch(category) {
    case ProductCategory.DAILY: return 2; // 
    case ProductCategory.FOOD: return 4;  // 
    case ProductCategory.ELECTRONICS: return 6; // 
    case ProductCategory.LUXURY: return 8; // 
    case ProductCategory.COLLECTIBLE: return 10; // 
    default: return 5;
  }
}

/**
 * ID
 * @param {string} category 
 * @returns {Array} ID
 */
function getAvailableLocations(category) {
  switch(category) {
    case ProductCategory.DAILY:
      return ['second_hand_market', 'commodity_market'];
    case ProductCategory.FOOD:
      return ['commodity_market'];
    case ProductCategory.ELECTRONICS:
      return ['electronics_hub', 'premium_mall'];
    case ProductCategory.LUXURY:
      return ['premium_mall'];
    case ProductCategory.COLLECTIBLE:
      return ['black_market'];
    default:
      return ['second_hand_market'];
  }
}

/**
 * 
 * @param {Object} location 
 * @param {Array} allProducts 
 * @returns {Array} 
 */
export function getAvailableProducts(location, allProducts) {
  if (!location || !allProducts) return [];
  
  return allProducts.filter(product => 
    product.availableAt.includes(location.id)
  );
}

