/**
 * 
 */
export const EventType = {
  RANDOM: 'random',       // 
  STORY: 'story',         // /
  LOCATION: 'location',   // 
  MARKET: 'market',       // 
  PERSONAL: 'personal',   // 
  TUTORIAL: 'tutorial'    // 
};

/**
 * 
 */
export const EffectType = {
  MONEY: 'money',                    // 
  DEBT: 'debt',                      // 
  INVENTORY: 'inventory',            // 
  MARKET: 'market',                  // 
  ATTRIBUTE: 'attribute',            // 
  LOCATION: 'location',              // 
  CAPACITY: 'capacity',              // 
  NEXT_EVENT: 'next_event',          // 
  GAME_STATE: 'game_state'           // 
};

/**
 * 
 * 
 */
export class Event {
  /**
   * 
   * @param {Object} options 
   * @param {string} options.id ID
   * @param {string} options.title 
   * @param {string} options.description 
   * @param {Array} options.options 
   * @param {Object} options.conditions 
   * @param {boolean} options.repeatable 
   * @param {string} options.type 
   * @param {number} options.weight 
   * @param {string} options.imageUrl URL
   */
  constructor({
    id,
    title,
    description,
    options = [],
    conditions = {},
    repeatable = false,
    type = EventType.RANDOM,
    weight = 1,
    imageUrl = null
  }) {
    this.id = id;
    this.title = title;
    this.description = description;
    this.options = options;
    this.conditions = conditions;
    this.repeatable = repeatable;
    this.type = type;
    this.weight = weight;
    // 
    this.imageUrl = null; // imageUrl;
  }
  
  /**
   * 
   * @param {Object} gameState 
   * @param {Array} triggeredEvents 
   * @returns {boolean} 
   */
  canTrigger(gameState, triggeredEvents = []) {
    const c = this.conditions;
    
    // 
    if (!this.repeatable && triggeredEvents.includes(this.id)) {
      return false;
    }
    
    // 
    if (c.minWeek !== null && gameState.currentWeek < c.minWeek) {
      return false;
    }
    
    if (c.maxWeek !== null && gameState.currentWeek > c.maxWeek) {
      return false;
    }
    
    // 
    if (c.locations !== null && !c.locations.includes(gameState.currentLocation?.id)) {
      return false;
    }
    
    // 
    if (c.playerMoney !== null) {
      const money = gameState.player.money;
      if (c.playerMoney.min !== undefined && money < c.playerMoney.min) return false;
      if (c.playerMoney.max !== undefined && money > c.playerMoney.max) return false;
    }
    
    // 
    if (c.playerDebt !== null) {
      const debt = gameState.player.debt;
      if (c.playerDebt.min !== undefined && debt < c.playerDebt.min) return false;
      if (c.playerDebt.max !== undefined && debt > c.playerDebt.max) return false;
    }
    
    // 
    if (c.inventoryItems !== null) {
      for (const item of c.inventoryItems) {
        const inventoryItem = gameState.player.inventory.find(i => i.productId === item.productId);
        const quantity = inventoryItem ? inventoryItem.quantity : 0;
        
        if (item.minQuantity !== undefined && quantity < item.minQuantity) return false;
        if (item.maxQuantity !== undefined && quantity > item.maxQuantity) return false;
      }
    }
    
    // 
    if (c.attributes !== null) {
      for (const [attr, limits] of Object.entries(c.attributes)) {
        const value = gameState.player.attributes?.[attr] || 0;
        if (limits.min !== undefined && value < limits.min) return false;
        if (limits.max !== undefined && value > limits.max) return false;
      }
    }
    
    // 
    if (c.requiredEvents !== null) {
      for (const eventId of c.requiredEvents) {
        if (!triggeredEvents.includes(eventId)) return false;
      }
    }
    
    // 
    if (c.excludedEvents !== null) {
      for (const eventId of c.excludedEvents) {
        if (triggeredEvents.includes(eventId)) return false;
      }
    }
    
    // 
    if (c.customCondition !== null && !c.customCondition(gameState)) {
      return false;
    }
    
    // 
    return Math.random() <= (c.probability || 1);
  }
  
  /**
   * 
   * @param {Object} gameState 
   * @returns {Array} 
   */
  getAvailableOptions(gameState) {
    return this.options.filter(option => {
      return option.condition === null || option.condition(gameState);
    });
  }
}

/**
 * 
 * 
 */
export class EventOption {
  /**
   * 
   * @param {Object} options 
   * @param {string} options.text 
   * @param {string} options.result 
   * @param {Object} options.effects 
   * @param {Function} options.condition 
   */
  constructor({
    text,
    result,
    effects,
    condition = null
  }) {
    this.text = text;
    this.result = result;
    this.effects = effects;
    this.condition = condition;
  }
  
  /**
   * 
   * @param {Object} gameState 
   * @returns {boolean} 
   */
  isAvailable(gameState) {
    return this.condition === null || this.condition(gameState);
  }
}

/**
 * 
 * 
 */
export class EventEffects {
  /**
   * 
   * @param {Object} options 
   * @param {number} options.money 1
   * @param {number} options.debt 1
   * @param {Array} options.inventory  [{productId, quantity, priceModifier}]
   * @param {Object} options.attributes  {businessSkill, creditRating, etc.}
   * @param {Object} options.market 
   * @param {number} options.market.globalPriceModifier 
   * @param {Object} options.market.categoryModifiers  {category: modifier}
   * @param {Object} options.market.productModifiers  {productId: modifier}
   * @param {Object} options.market.locationModifiers  {locationId: modifier}
   * @param {Object} options.market.locationProductModifiers  {locationId: {productId: modifier}}
   * @param {number} options.market.duration 
   * @param {boolean} options.forceLocationChange 
   * @param {string} options.targetLocation ID
   * @param {number} options.capacity 
   * @param {string} options.nextEvent ID
   * @param {Object} options.gameState  {gamePaused, forceGameOver, etc.}
   */
  constructor({
    money = 0,          // 1
    debt = 0,           // 1
    inventory = [],     //  [{productId, quantity, priceModifier}]
    attributes = {},    //  {businessSkill, creditRating, etc.}
    market = null,      //  {globalPriceModifier, categoryModifiers, productModifiers, locationModifiers, locationProductModifiers, duration}
    forceLocationChange = false, // 
    targetLocation = null, // ID
    capacity = 0,       // 
    nextEvent = null,   // ID
    gameState = null    //  {gamePaused, forceGameOver, etc.}
  }) {
    this.money = money;
    this.debt = debt;
    this.inventory = inventory;
    this.attributes = attributes;
    this.market = market;
    this.forceLocationChange = forceLocationChange;
    this.targetLocation = targetLocation;
    this.capacity = capacity;
    this.nextEvent = nextEvent;
    this.gameState = gameState;
  }
}

/**
 * 
 * 
 */
export class EventConditions {
  /**
   * 
   * @param {Object} options 
   */
  constructor({
    minWeek = null,        // 
    maxWeek = null,        // 
    locations = null,      // 
    probability = 1,       // 
    playerMoney = null,    //  {min, max}
    playerDebt = null,     //  {min, max}
    inventoryItems = null, //  [{productId, minQuantity, maxQuantity}]
    attributes = null,     //  {attributeName: {min, max}}
    requiredEvents = null, // 
    excludedEvents = null, // 
    customCondition = null //  (gameState) => boolean
  }) {
    this.minWeek = minWeek;
    this.maxWeek = maxWeek;
    this.locations = locations;
    this.probability = probability;
    this.playerMoney = playerMoney;
    this.playerDebt = playerDebt;
    this.inventoryItems = inventoryItems;
    this.attributes = attributes;
    this.requiredEvents = requiredEvents;
    this.excludedEvents = excludedEvents;
    this.customCondition = customCondition;
  }
}

// API
/**
 *  - OOP
 * @param {Object} options 
 * @returns {Event} 
 */
export function createEvent(options) {
  if (typeof options === 'object' && !Array.isArray(options)) {
    return new Event(options);
  } else {
    // API
    const [id, title, description, options, conditions, repeatable, type, weight, imageUrl] = arguments;
    return new Event({
      id, title, description, options, conditions, repeatable, type, weight, imageUrl
    });
  }
}

/**
 *  - API
 * @param {string} text 
 * @param {string} result 
 * @param {Object} effects 
 * @param {Function} condition 
 * @returns {EventOption} 
 */
export const createEventOption = (text, result, effects, condition = null) => {
  return new EventOption({ text, result, effects, condition });
};

/**
 *  - API
 * @returns {EventEffects} 
 */
export const createEventEffects = (options) => {
  return new EventEffects(options);
};

/**
 *  - API
 * @returns {EventConditions} 
 */
export const createEventConditions = (options) => {
  return new EventConditions(options);
}; 
