/**
 * ID
 * @param {string} id ID
 * @returns {Event|null} null
 */
export function getEventById(id) {
  const eventData = predefinedEvents.find(e => e.id === id);
  if (!eventData) return null;
  
  // EventOption
  const options = eventData.options.map(option => {
    if (!(option instanceof EventOption)) {
      return new EventOption(option);
    }
    return option;
  });
  
  // EventimageUrl
  return new Event({
    ...eventData,
    options,
    imageUrl: eventData.imageUrl || (typeof eventData[8] === 'string' ? eventData[8] : null) // 9imageUrl
  });
}