// 

/**
 * 
 * 
 */
export const PriceTrend = {
  RISING_STRONG: 'rising_strong',  // 
  RISING: 'rising',                // 
  STABLE_HIGH: 'stable_high',      // 
  STABLE: 'stable',                // 
  STABLE_LOW: 'stable_low',        // 
  FALLING: 'falling',              // 
  FALLING_STRONG: 'falling_strong',// 
  VOLATILE: 'volatile'             // 
};

// 
const priceCalculationCache = new Map();

/**
 * 
 * Math.sin20%
 * 
 * @param {number} x - 
 * @returns {number} 
 */
function fastSin(x) {
  // [0, 2]
  x = x % (2 * Math.PI);
  
  // 
  if (x === 0 || x === Math.PI) return 0;
  if (x === Math.PI/2) return 1;
  if (x === 3*Math.PI/2) return -1;
  
  // 
  // sin(x)  x - x^3/3! + x^5/5! - x^7/7!
  const x2 = x * x;
  const x3 = x2 * x;
  const x5 = x3 * x2;
  const x7 = x5 * x2;
  
  return x - x3/6 + x5/120 - x7/5040;
}

/**
 * 
 * Math.cos20%
 * 
 * @param {number} x - 
 * @returns {number} 
 */
function fastCos(x) {
  // [0, 2]
  x = x % (2 * Math.PI);
  
  // 
  if (x === 0) return 1;
  if (x === Math.PI/2 || x === 3*Math.PI/2) return 0;
  if (x === Math.PI) return -1;
  
  // 
  // cos(x)  1 - x^2/2! + x^4/4! - x^6/6!
  const x2 = x * x;
  const x4 = x2 * x2;
  const x6 = x4 * x2;
  
  return 1 - x2/2 + x4/24 - x6/720;
}

/**
 * 
 * 
 * 
 * @param {Object} product - 
 * @param {number} week - 
 * @param {Object} previousPrice - 
 * @param {number} locationFactor - 
 * @param {Object} marketModifiers - 
 * @returns {Object}  {price, trend, changePercent}
 */
export function calculatePriceWithCache(product, week, previousPrice, locationFactor, marketModifiers) {
  // 
  const cacheKey = `${product.id}_${week}_${previousPrice?.price || 0}_${locationFactor}_${JSON.stringify(marketModifiers)}`;
  
  // 
  if (priceCalculationCache.has(cacheKey)) {
    return priceCalculationCache.get(cacheKey);
  }
  
  // 
  const result = calculateNewPrice(product, week, previousPrice, locationFactor, marketModifiers);
  
  // 
  priceCalculationCache.set(cacheKey, result);
  
  return result;
}

/**
 * 
 * 
 */
export function clearPriceCache() {
  priceCalculationCache.clear();
  console.log('');
}

/**
 * 
 * 
 * 
 * @param {Object} product - 
 * @param {number} week - 
 * @param {Object} previousPrice - 
 * @param {number} locationFactor - 
 * @param {Object} marketModifiers - 
 * @returns {Object} {price: , trend: , changePercent: }
 */
export function calculateNewPrice(product, week, previousPrice = null, locationFactor = 1, marketModifiers = {}) {
  // 1. 
  const prevPrice = previousPrice?.price || product.basePrice;
  const prevTrend = previousPrice?.trend || PriceTrend.STABLE;
  const productId = product.id;
  const minPrice = product.minPrice;
  const maxPrice = product.maxPrice;
  const priceRange = maxPrice - minPrice;
  const volatility = product.volatility;
  const category = product.category;
  
  // 2. 
  const weekModForSin = week & 0x3ff; //  week % 1024
  
  // 3.  - 
  const volatilityBase = 0.6 + (volatility * 0.15); // 0.0666670.15
  
  // 4.  - 
  const trendFactorTable = {
    [PriceTrend.RISING]: 0.05,        // 
    [PriceTrend.RISING_STRONG]: 0.08,  // 
    [PriceTrend.FALLING]: -0.12,       // 
    [PriceTrend.FALLING_STRONG]: -0.15,// 
    [PriceTrend.STABLE]: -0.01,        // 
    [PriceTrend.STABLE_HIGH]: -0.04,   // 
    [PriceTrend.STABLE_LOW]: 0.02,     // 
    [PriceTrend.VOLATILE]: -0.02       // 
  };
  
  // 
  // ID
  const randomSeed = (productId * 1000 + week) % 997; // 997
  const normalizedSeed = randomSeed / 997; // 0-1
  
  // 75%25%
  const randomFactor = Math.random();
  let trendContinuationFactor;
  
  // ID
  if (normalizedSeed < 0.3) {
    // 30%
    trendContinuationFactor = randomFactor < 0.75 
      ? trendFactorTable[prevTrend] || 0 
      : (Math.random() - 0.35) * 0.08;  // 
  } else {
    // 70%
    trendContinuationFactor = randomFactor < 0.75 
      ? trendFactorTable[prevTrend] || 0 
      : (Math.random() - 0.65) * 0.08;  // 
  }
  
  // 5.  - 
  const categoryFactor = (() => {
    console.log(':', {
      category,
      weekMod: weekModForSin
    });
    
    let factor = 0;
    switch(category) {
      case 'DAILY': 
        factor = fastSin(weekModForSin * 0.077) * 0.1 - 0.03; // 
        break;
      case 'FOOD': 
        factor = fastSin(weekModForSin * 0.167) * 0.2 - 0.05;  // 
        break;
      case 'ELECTRONICS': 
        factor = fastSin(weekModForSin * 0.1) * 0.15 - 0.08; // 
        break;
      case 'LUXURY': 
        factor = fastSin(weekModForSin * 0.067) * 0.25 - 0.03; // 
        break;
      case 'COLLECTIBLE': 
        const sinPart = fastSin(weekModForSin * 0.125) * 0.3 - 0.04; // 
        const cosPart = Math.cos(weekModForSin * 0.2) * 0.15 - 0.02; // 
        factor = sinPart + cosPart;
        break;
      default: 
        factor = 0;
    }
    
    // ID
    const idOffset = ((productId % 10) - 5) * 0.01; // -0.05  0.05 
    factor += idOffset;
    
    console.log(':', factor);
    return factor;
  })();
  
  // 6.  - sin
  // ID
  const phaseOffset = (productId % 10) * 0.1;
  const periodFactor = fastSin((weekModForSin + phaseOffset) * 0.1 + ((productId & 0xF) * 0.01)) * 0.12 - 0.03; // 
  
  // 7. 
  const locationAdjustment = locationFactor || 1;
  const isSpecial = marketModifiers.specialProducts?.includes(productId) ? 0.8 : 1; // 0.850.8
  
  //  - ID
  const noiseBase = Math.sin(productId * week * 0.01) * 0.5 + 0.5; // 0-1
  const randomNoise = 0.90 + noiseBase * 0.16; // 0.920.90
  
  const globalMarketModifier = marketModifiers.globalPriceModifier || 1;
  const categoryModifier = marketModifiers.categoryModifiers?.[category] || 1;
  const productModifier = marketModifiers.productModifiers?.[productId] || 1;

  // 
  const currentLocationId = marketModifiers.currentLocationId; // ID
  let locationModifier = 1;
  let locationProductModifier = 1;

  // ID
  if (currentLocationId) {
    // 
    locationModifier = marketModifiers.locationModifiers?.[currentLocationId] || 1;

    // 
    if (marketModifiers.locationProductModifiers?.[currentLocationId]?.[productId]) {
      locationProductModifier = marketModifiers.locationProductModifiers[currentLocationId][productId];
    }
    
    // 
    if (locationModifier !== 1 || locationProductModifier !== 1) {
      console.log(` - : ${currentLocationId}, : ${productId}, : ${locationModifier.toFixed(2)}, : ${locationProductModifier.toFixed(2)}`);
    }
  }

  // 8. 
  const totalFactor = volatilityBase * 
    (1 + trendContinuationFactor) * 
    (1 + categoryFactor) *
    (1 + periodFactor) * 
    locationAdjustment * 
    isSpecial * 
    randomNoise * 
    globalMarketModifier * 
    categoryModifier * 
    productModifier * 
    locationModifier * // 
    locationProductModifier; // 

  // 
  const minChangeRate = 0.005; // 0.5%
  let adjustedTotalFactor = totalFactor;
  
  // 
  if (Math.abs(totalFactor - 1) < minChangeRate) {
    // ID
    const randomDirection = ((productId * week) % 2 === 0) ? 1 : -1;
    adjustedTotalFactor = 1 + (randomDirection * (minChangeRate + Math.random() * 0.01));
    console.log(` ${productId}  ${((adjustedTotalFactor - 1) * 100).toFixed(2)}%`);
  }

  // 9. 
  let newPrice = prevPrice * adjustedTotalFactor;
  
  // 10. 
  newPrice = applyPriceProtection(newPrice, prevPrice, product, marketModifiers);

  // 11. 
  newPrice = Math.max(minPrice, Math.min(newPrice, maxPrice));

  // 12.  - 
  const originalPricePercent = (newPrice - product.basePrice) / product.basePrice;
  const trend = determineTrendFast(originalPricePercent, newPrice, minPrice, maxPrice);

  // 13. 
  return {
    price: Math.round(newPrice),
    trend,
    changePercent: parseFloat((originalPricePercent * 100).toFixed(1)),
    // 
    originalPricePercent: originalPricePercent
  };
}

/**
 * 
 * 
 * 
 * @param {number} newPrice 
 * @param {number} prevPrice 
 * @param {Object} product 
 * @param {Object} marketModifiers 
 * @returns {number} 
 */
function applyPriceProtection(newPrice, prevPrice, product, marketModifiers) {
  // 
  const volatilityFactor = product.volatility / 10;  // 0.1 - 1.0
  const volatilityMultiplier = marketModifiers.volatilityMultiplier || 1.0;
  
  // 
  const baseMaxChange = 0.20;  // 12%20%
  const maxChange = baseMaxChange * (1 + volatilityFactor) * volatilityMultiplier;
  
  // 
  const maxUpLimit = prevPrice * (1 + maxChange);
  const maxDownLimit = prevPrice * (1 - maxChange);
  
  // 
  const minChangeAmount = prevPrice * 0.005; // 0.5%
  
  // 
  if (Math.abs(newPrice - prevPrice) < minChangeAmount) {
    // ID
    const direction = (product.id % 2 === 0) ? 1 : -1;
    newPrice = prevPrice * (1 + direction * (0.005 + Math.random() * 0.01)); // 0.5%-1.5%
  }
  
  // 
  let protectedPrice = Math.max(maxDownLimit, Math.min(newPrice, maxUpLimit));
  
  // 
  const midPoint = product.minPrice + (product.maxPrice - product.minPrice) * 0.5;
  const distanceFromMid = Math.abs(protectedPrice - midPoint) / (product.maxPrice - product.minPrice);
  
  // 
  if (distanceFromMid > 0.4) {
    const regressionStrength = (distanceFromMid - 0.4) * 0.05; // 
    const regressionDirection = protectedPrice > midPoint ? -1 : 1;
    const regressionAdjustment = regressionStrength * (product.maxPrice - product.minPrice) * regressionDirection;
    
    protectedPrice += regressionAdjustment;
  }
  
  return protectedPrice;
}

/**
 * 
 * @param {number} changePercent - 
 * @param {number} price - 
 * @param {number} minPrice - 
 * @param {number} maxPrice - 
 * @returns {string} 
 */
function determineTrendFast(changePercent, price, minPrice, maxPrice) {
  //  - 
  const STRONG_RISE_THRESHOLD = 0.15;  // 10%15%
  const RISE_THRESHOLD = 0.05;         // 3%5%
  const STRONG_FALL_THRESHOLD = -0.15; // -10%-15%
  const FALL_THRESHOLD = -0.05;        // -3%-5%
  
  //  - 
  if (changePercent > STRONG_RISE_THRESHOLD) return PriceTrend.RISING_STRONG;
  if (changePercent > RISE_THRESHOLD) return PriceTrend.RISING;
  if (changePercent < STRONG_FALL_THRESHOLD) return PriceTrend.FALLING_STRONG;
  if (changePercent < FALL_THRESHOLD) return PriceTrend.FALLING;
  
  //  - 
  const priceRange = maxPrice - minPrice;
  if (priceRange <= 0) return PriceTrend.STABLE; // 
  
  const relativePricePos = (price - minPrice) / priceRange;
  
  // 
  if (relativePricePos > 0.8) return PriceTrend.STABLE_HIGH;
  if (relativePricePos < 0.2) return PriceTrend.STABLE_LOW;
  return PriceTrend.STABLE;
}

/**
 * 
 * 
 * 
 * @param {Array} products 
 * @param {number} currentWeek 
 * @param {Object} priceHistory 
 * @param {Object} marketModifiers 
 * @returns {Object} 
 */
export function batchUpdatePrices(products, currentWeek, priceHistory = {}, marketModifiers = {}) {
  console.time('BatchPriceUpdate');
  
  // IDmarketModifiers
  if (marketModifiers.currentLocation) {
    marketModifiers.currentLocationId = marketModifiers.currentLocation.id;
  }
  
  const result = {};
  const productCount = products.length;
  
  console.log(` - : ${productCount}, : ${currentWeek}, :`, {
    global: marketModifiers.globalPriceModifier || 1,
    categories: Object.keys(marketModifiers.categoryModifiers || {}).length,
    products: Object.keys(marketModifiers.productModifiers || {}).length,
    locations: Object.keys(marketModifiers.locationModifiers || {}).length,
    locationProducts: Object.keys(marketModifiers.locationProductModifiers || {}).reduce((count, locationId) => {
      return count + Object.keys(marketModifiers.locationProductModifiers[locationId] || {}).length;
    }, 0),
    currentLocationId: marketModifiers.currentLocationId
  });
  
  for (let i = 0; i < productCount; i++) {
    const product = products[i];
    const productId = product.id;
    
    // 
    const previousPrice = priceHistory[productId] || null;
    
    // 
    try {
      result[productId] = calculatePriceWithCache(
        product,
        currentWeek,
        previousPrice,
        1, // locationFactor1marketModifiers
        marketModifiers
      );
    } catch (error) {
      console.error(` ${productId} :`, error);
      // 
      result[productId] = {
        price: product.basePrice,
        trend: 'stable',
        changePercent: 0
      };
    }
  }
  
  console.timeEnd('BatchPriceUpdate');
  return result;
}

/**
 * 
 * 
 * 
 * @param {Array} products - 
 * @param {Object} location - 
 * @param {Object} priceHistory - 
 * @param {number} week - 
 * @param {Object} marketModifiers - 
 * @returns {Array} 
 */
export function generateLocationProducts(products, location, priceHistory, week, marketModifiers = {}) {
  // 
  if (!location) {
    return products.map(product => {
      const priceData = priceHistory[product.id] || { price: product.basePrice, trend: PriceTrend.STABLE };
      return { ...product, currentPrice: priceData.price, trend: priceData.trend };
    });
  }
  
  // 
  const specialProductSet = new Set(location.specialties || []);
  
  // marketModifiers
  const locationMarketModifiers = { 
    ...marketModifiers,
    specialProducts: location.specialties || [],
    currentLocationId: location.id // ID
  };
  
  // 
  return products.map(product => {
    const isSpecial = specialProductSet.has(product.id);
    const locationFactor = isSpecial ? 0.85 : 1.0; // 15%
    
    const priceData = priceHistory[product.id] || { 
      price: product.basePrice, 
      trend: PriceTrend.STABLE 
    };
    
    // 
    const locationPrice = Math.round(priceData.price * locationFactor);
    
    // 
    return { 
      ...product, 
      currentPrice: locationPrice, 
      trend: priceData.trend,
      isSpecial: isSpecial
    };
  });
}

/**
 * 
 * @param {string} trend - 
 * @returns {string} 
 */
export function getTrendDescription(trend) {
  const descriptions = {
    [PriceTrend.RISING_STRONG]: '',
    [PriceTrend.RISING]: '',
    [PriceTrend.STABLE_HIGH]: '',
    [PriceTrend.STABLE]: '',
    [PriceTrend.STABLE_LOW]: '',
    [PriceTrend.FALLING]: '',
    [PriceTrend.FALLING_STRONG]: '',
    [PriceTrend.VOLATILE]: ''
  };
  
  return descriptions[trend] || '';
}

/**
 * 
 * @param {string} trend - 
 * @returns {string} 
 */
export function getTrendIcon(trend) {
  const icons = {
    [PriceTrend.RISING_STRONG]: '',
    [PriceTrend.RISING]: '',
    [PriceTrend.STABLE_HIGH]: '',
    [PriceTrend.STABLE]: '',
    [PriceTrend.STABLE_LOW]: '',
    [PriceTrend.FALLING]: '',
    [PriceTrend.FALLING_STRONG]: '',
    [PriceTrend.VOLATILE]: ''
  };
  
  return icons[trend] || '';
}

/**
 * 
 * @param {string} trend - 
 * @returns {string} 
 */
export function getTrendColor(trend) {
  const colors = {
    [PriceTrend.RISING_STRONG]: '#e53935', // 
    [PriceTrend.RISING]: '#f44336', // 
    [PriceTrend.STABLE_HIGH]: '#ff9800', // 
    [PriceTrend.STABLE]: '#9e9e9e', // 
    [PriceTrend.STABLE_LOW]: '#8bc34a', // 
    [PriceTrend.FALLING]: '#4caf50', // 
    [PriceTrend.FALLING_STRONG]: '#2e7d32', // 
    [PriceTrend.VOLATILE]: '#9c27b0' // 
  };
  
  return colors[trend] || '#9e9e9e';
}

/**
 *  - 
 * 
 * 
 * @param {Array} products - 
 * @param {number} week - 
 * @param {Object} priceHistory - 
 * @param {Object} marketModifiers - 
 */
export function measurePriceCalculationPerformance(products, week, priceHistory, marketModifiers) {
  if (process.env.NODE_ENV === 'development') {
    console.log('...');
    
    // 
    const startOriginal = performance.now();
    products.forEach(product => {
      const prevPrice = priceHistory[product.id];
      calculateNewPrice(product, week, prevPrice, 1, marketModifiers);
    });
    const endOriginal = performance.now();
    
    // 
    clearPriceCache();
    
    // 
    const startOptimized = performance.now();
    products.forEach(product => {
      const prevPrice = priceHistory[product.id];
      calculatePriceWithCache(product, week, prevPrice, 1, marketModifiers);
    });
    const endOptimized = performance.now();
    
    // 
    const startBatch = performance.now();
    batchUpdatePrices(products, week, priceHistory, marketModifiers);
    const endBatch = performance.now();
    
    // 
    console.log(`: ${(endOriginal - startOriginal).toFixed(2)}ms`);
    console.log(`: ${(endOptimized - startOptimized).toFixed(2)}ms`);
    console.log(`: ${(endBatch - startBatch).toFixed(2)}ms`);
    console.log(`: ${((endOriginal - startOriginal) / (endOptimized - startOptimized)).toFixed(2)}x`);
  }
} 